/* COLORS */

$p2020-pink: #c9356e;
$p2020-blue: #2271b1;

$p2020-white: #fff;
$p2020-black: #000;

// TODO: Add color scales

/* TYPOGRAPHY */

$font-sans: inter, -apple-system, system-ui, blinkmacsystemfont, "Segoe UI", "Roboto", "Oxygen-Sans", "Ubuntu", "Cantarell", "Helvetica Neue", sans-serif;
$font-mono: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", "Courier New", monospace;

/* BREAKPOINTS */
$web: 1200px;
$tablet-landscape: 1024px;
$tablet-portrait: 876px;
$handset-landscape: 640px;
$handset-portrait: 550px;

$breakpoints: (
	"web": $web,
	"tablet-landscape": $tablet-landscape,
	"tablet-portrait": $tablet-portrait,
	"handset-landscape": $handset-landscape,
	"handset-portrait": $handset-portrait,
);
