/* LINKS */
// very basic styles up for links when no other styles are present. Mostly colors.

a,
a:visited,
a:active {
	color: var(--color-link);
}

a:hover,
h1 a:hover {
	color: var(--color-link-dark);
}

.entry-content p a,
.comment-content p a,
.entry-content li a,
.comment-content li a {
	text-decoration: underline;
}

// TODO: Clean up
.entry-meta .entry-actions:hover a,
.comment-meta .comment-actions:hover a,
.entry-meta .entry-actions:hover a:visited,
.comment-meta .comment-actions:hover a:visited,
#help dt, /* TODO: Not a link */
#media-buttons .button,
.responsive-link {
	color: var(--color-link);
}

// TODO: Clean up
.entry-meta .entry-actions:hover a:hover,
.comment-meta .comment-actions:hover a:hover,
#media-buttons .button:hover,
.responsive-link {
	color: var(--color-link-dark);
}
