* {
	box-sizing: border-box;
}

html {
	overflow-y: scroll; /* Keeps page centred in all browsers regardless of content height */
	-webkit-text-size-adjust: 100%; /* Prevents iOS text size adjust after orientation change, without disabling user zoom */
	-ms-text-size-adjust: 100%; /* www.456bereastreet.com/archive/201012/controlling_text_size_in_safari_for_ios_without_disabling_user_zoom/ */
	font-size: 100%;
}

body {
	-webkit-overflow-scrolling: touch;
	margin: 0;
	background: var(--color-background);
	color: var(--color-text);
	font-family: var(--font-default);
	line-height: 1.6;
}

div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
figure,
font,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
	border: 0;
	font-family: inherit;
	font-style: inherit;
	font-weight: inherit;
	margin: 0;
	outline: 0;
	padding: 0;
	vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
nav,
section {
	display: block;
}

hr {
	background-color: var(--color-border);
	border: 0;
	height: 1px;
	margin-bottom: 1.2em;
}

/**
 * Text
 */
p {
	margin: 1.2em 0;
}

article {
	font-family: var(--font-body);
	font-size: pxrem(15);
}

b,
strong {
	font-weight: 600;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

address {
	margin: 0 0 1.5em;
}

abbr,
acronym {
	cursor: help;
}

mark,
ins {
	background: var(--color-background);
	text-decoration: none;
}

sup,
sub {
	font-size: 75%;
	height: 0;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sup {
	bottom: 1ex;
}

sub {
	top: 0.5ex;
}

small {
	font-size: 75%;
}

big {
	font-size: 125%;
}

/**
 * Lists
 */
ul,
ol {
	margin: 0 0 1.2em 2.4em;
	padding-left: 0;
	line-height: 1.6;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.2em;
}

dt {
	font-family: var(--font-default);
	font-weight: 600;
}

dd {
	margin: 0 1.2em 1.2em;
}

/**
 * Images
 */
img {
	height: auto; /* Make sure images are scaled correctly. */
	max-width: 100%; /* Limit to container width. */
}

/**
 * Links
 */
a {
	text-decoration: none;

	&:focus {
		outline: thin dotted;
	}

	&:hover,
	&:active { /* Improves readability when focused and also mouse hovered in all browsers people.opera.com/patrickl/experiments/keyboard/test */
		outline: 0;
	}

	img {
		border: 0;
	}
}

/**
 * Monospaced
 */
code,
pre,
kbd,
tt,
var {
	font-family: var(--font-mono);
}

code {
	border-radius: 3px;
	background: var(--color-background);
	padding: 0.4em 0.65em;
	font-size: 0.8em;
}

pre {
	background: var(--color-background);
	margin-bottom: 1.2em;
	padding: 1.2em;
	overflow: auto;
	max-width: 100%;
}

/**
 * Quotes
 */
blockquote,
q {
	quotes: "" "";

	&::before,
	&::after {
		content: "";
	}
}

blockquote {
	background: var(--color-background);
	font-style: italic;
	margin: 0 0 1.2em;
	padding: 1.2em 1.2em 0 1.2em;
	overflow: auto;

	code {
		background: var(--color-background-light);
	}

	em {
		font-style: normal;
	}

	cite {
		display: block;
		text-align: right;

		&::before {
			content: "—\0000a0"; // em dash and non-breaking space
		}
	}

	&.wp-embedded-content {
		padding: 1.2em;
	}
}

/**
 * Tables
 */
table { /* tables still need 'cellspacing="0"' in the markup */
	margin: 0 0 1.2em;
	width: 100%;
	text-align: left;
	border-collapse: separate;
	border-spacing: 0;
}

th {
	font-weight: 600;
}

/**
 * Inputs
 */
button,
input,
select,
textarea {
	font-size: 100%; /* Corrects font size not being inherited in all browsers */
	margin: 0; /* Addresses margins set differently in IE6/7, F3/4, S5, Chrome */
	vertical-align: baseline; /* Improves appearance and consistency in all browsers */
}

textarea {
	overflow: auto; /* Removes default vertical scrollbar in IE6/7/8/9 */
	vertical-align: top; /* Improves readability and alignment in all browsers */
	width: 98%;
}

input[type="checkbox"],
input[type="radio"] {
	padding: 0; /* Addresses excess padding in IE8/9 */
}

input[type="search"] {
	-webkit-appearance: textfield; /* Addresses appearance set to searchfield in S5, Chrome */

	&::-webkit-search-decoration { /* Corrects inner padding displayed oddly in S5, Chrome on OSX */
		-webkit-appearance: none;
	}
}
