@mixin gridicon($name, $size, $color) {
	content: "";
	display: inline-block;
	mask: url(img/gridicons/gridicons-#{$name}.svg) no-repeat;
	mask-size: cover;
	width: $size;
	height: $size;
	background: $color;
}

@mixin breakpoint( $size ) {
	@each $breakpoint, $value in $breakpoints {
		@if $size == $breakpoint {
			@media ( max-width: map-get($breakpoints, $breakpoint) ) {
				@content;
			}
		}
	}
}
