:root {

	/* COLORS */
	// TODO: Use color scale variables
	--color-accent: #{$p2020-pink};
	--color-accent-dark: #{darken($p2020-pink, 5%)};

	--color-text: #1a2532;
	--color-text-medium: #6c798a;
	--color-text-light: #7e8896;

	--color-link: #{$p2020-blue};
	--color-link-dark: #{darken($p2020-blue, 10%)};

	--color-border: #eef0f4;
	--color-border-dark: #{darken(#eef0f4, 10%)};

	--color-background: #eef0f4;
	--color-background-dark: #2a394b;
	--color-background-light: #{lighten(#eef0f4, 4%)};

	--color-white: #{$p2020-white};
	--color-black: #{$p2020-black};

	/* TYPOGRAPHY */
	--font-default: #{$font-sans};
	--font-body: #{$font-sans};
	--font-mono: #{$font-mono};
}
