.o2-comment {
	margin: 0;
	padding: 0.8em;
	border-top: 1px solid var(--color-border);

	@include breakpoint( "handset-portrait" ) {
		margin-top: 5px !important;

		&#respond {
			padding: 20px 0 20px 5px !important;
			border-left: 0 !important;
		}

		.o2-comment {
			border-top-width: 0 !important;
			border-left: 2px solid var(--color-border);
			padding-left: 4px !important;
			padding-top: 0;
			margin-bottom: 20px;

			.o2-comment .o2-comment {
				border-left: 2px solid var(--color-border);

				.o2-comment {
					border-left: none;
				}
			}
		}

	}

	p {
		margin: 0 0 1.2em 0;

		&:empty {
			margin-bottom: 0;
		}
	}

	hr {
		margin-left: 0;
		margin-right: 0;
	}
}

.o2-comment-awaiting-approval {
	font-style: italic;
	color: var(--color-text-light);
}

.o2-display-comments-toggle {
	.genericon {
		position: relative;
		top: 4px;
	}
}

.comment-content {
	margin-left: 65px;

	@include breakpoint( "handset-portrait" ) {
		margin-left: 0;
	}
}

.o2-child-views {
	.comment-content {
		margin-left: 45px;

		@include breakpoint( "handset-portrait" ) {
			margin-left: 0;
		}
	}
}

.o2-post-comments {
	.o2-comment {
		position: relative;

		.comment-content {
			padding-left: 0;
			padding-right: 1.6em;
			margin-left: 3.5em;
		}

		.o2-logged-out-editor {
			margin-top: 0;
		}

		.o2-child-comments {
			padding-left: 1.5em;
		}
	}

	> .o2-comment {

		/* Make sure we have padding on the last top level comment */
		&:last-child {
			padding-bottom: 1em;
		}

		&:nth-of-type(odd) {
			background: transparent;
		}

		&nth-of-type(even) {
			background: transparent;
		}
	}

	/* Override padding for every last child */
	.o2-comment:last-child {
		margin-bottom: 0;
	}

}

#respond {
	.o2-timestamp {
		display: none;
	}

	.o2-comment-header {
		padding-bottom: 0;
	}
}

/* COMMENTS */
.comment-content a {
	word-wrap: break-word;
}

#comments {
	margin: 0 15px;

	@include breakpoint( "handset-portrait" ) {

		margin-left: 0;

	}
}

@include breakpoint( "handset-portrait" ) {
	.o2-child-comments {
		padding: 0.5em 0 0 0 !important;


		.o2-comment {
			padding-left: 1.5em;

			&:last-of-type {
				margin-bottom: 0;
			}
		}

	}
}


article.tag-p2-xpost {
	.comment {
		.avatar {
			margin-left: -10px;
			margin-top: 0;
		}
	}
}
