button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid var(--color-border);
	border-radius: 2px;
	background: var(--color-background);
	box-shadow: inset 0 1px 0 rgba($p2020-white, 0.5), inset 0 15px 17px rgba($p2020-white, 0.5), inset 0 -5px 12px rgba($p2020-black, 0.05);
	color: var(--color-text);
	cursor: pointer; /* Improves usability and consistency of cursor style between image-type 'input' and others */
	-webkit-appearance: button; /* Corrects inability to style clickable 'input' types in iOS */
	line-height: 1;
	padding: 0.6em 1em;
	font-family: var(--font-default);

	&:hover {
		box-shadow: inset 0 1px 0 rgba($p2020-white, 0.8), inset 0 15px 17px rgba($p2020-white, 0.8), inset 0 -5px 12px rgba($p2020-black, 0.02);
	}

	&:focus,
	&:active {
		box-shadow: inset 0 -1px 0 rgba($p2020-white, 0.5), inset 0 2px 5px rgba($p2020-black, 0.15);
	}
}
