.o2-editor-footer {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	padding: 16px 0;

	@at-root .o2-app-new-post & {
		padding-left: 16px;
		padding-right: 16px;
	}

	.o2-post-form-options,
	.o2-comment-form-options {
		order: -1;
		margin: 0 24px 0 0;
		padding: 0;
		color: var(--color-text-light);
		font-size: pxrem(12);
	}

	.post-subscription-form {
		display: none;
	}

	.comment-subscription-form {
		margin: 0 !important;
	}

	#post_subscribe_label {
		margin-bottom: 0 !important;
	}

	a.o2-save {
		@extend %p2020-button--primary;
		order: 2;
	}

	a.o2-cancel,
	a.o2-new-comment-cancel {
		@extend %p2020-button--borderless;

	}

	a.o2-comment-save { // Reply button
		@extend %p2020-button--primary;
		order: 3;

		margin-left: 8px;
	}
}
