/* stylelint-disable no-descending-specificity */

/**
 * General layout
 */

/* POSTS */

article.post,
article.page,
article#post-new {

	background: var(--color-white);
	margin: 0 0 30px;
	padding: 1.6em 0;

	p {
		margin: 0 0 1.2em 0;

		&:empty {
			margin-bottom: 0;
		}
	}

	.o2-post {
		padding: 1.6em;
		overflow-wrap: break-word;
	}

	.entry-content {
		h2,
		h3,
		h4,
		h5 {
			margin: 20px 0;
		}

		table {
			border-top: 1px solid var(--color-border-dark);

			tr {
				td,
				th {
					padding: 1em;
					border-bottom: 1px solid var(--color-border);
				}
			}

			thead,
			tfoot {
				tr {
					td,
					th {
						font-weight: 700;
					}
				}
			}
		}

		table:not(.wp-block-table) {
			td:first-child,
			th:first-child {
				padding-left: 0;
			}

			td:last-child,
			th:last-child {
				padding-right: 0;
			}
		}

		@include breakpoint( "handset-portrait" ) {
			margin: 0;
		}

	}
}

.o2 {
	article.post,
	article#post-new,
	article.page {

		padding: 0;

		&.sticky {
			border: 0;
			background: var(--color-white);
		}
	}
}

@include breakpoint( "tablet-portrait" ) {
	input[type="checkbox"] {
		font-size: 2em;
	}
}

.tag-prefix,
.mentions-prefix {
	opacity: 0.4;
}

.o2-tags-array {
	display: block;
	padding: 0.2em 0;
}

.ui-helper-hidden-accessible {
	display: none;
}

.post:first-of-type {
	&.tag-p2-xpost {
		margin-top: 2em;
	}
}

.tag-p2-xpost {
	background: transparent;
	padding-bottom: 0;
	margin: -1.5em 0 2em 0;

	.avatar {
		float: left;
		border: 1px rgba($p2020-white, 0.5) solid;
		box-shadow: 0 1px 2px rgba($p2020-black, 0.1);
	}

	.o2-post,
	.o2-comment {
		font-size: 0.9em;
		font-style: italic;
		padding: 10px 10px 10px 0;

		.avatar {
			width: 34px;
		}
	}

	.o2-post {
		background: transparent;
		min-height: 36px;
		padding-left: 10px;
	}

	.author-avatar {
		display: block;

		img {
			margin-right: 10px;
		}
	}

	.o2-comment {
		padding-left: 5em;

		.o2-comment-header {
			overflow: visible;
			padding-bottom: 0;
		}

		.comment-meta {
			line-height: 1.4;
		}

		.o2-actions {
			display: none;
		}
	}

	.o2-tags-array,
	.sharedaddy {
		display: none;
	}

	p {
		margin: 0;
	}

	.comment-likes {
		display: none;
	}

}

.o2-xpost-author {
	font-size: 1em;
	overflow: hidden;

	a {
		padding: 0;
	}

	.entry-author {
		font-size: 1em;
	}

	.entry-date {
		color: var(--color-text-light);
		font-size: 0.9em;
		font-weight: 300;
		padding-left: 5px;
	}
}

.o2-xpost-avatar {
	border-width: 0;
	box-shadow: none;
}

.o2-xpost-entry-date {
	float: none;

	&:hover {
		color: var(--color-link);
	}
}

.format-status .o2-post .entry-title,
.tag-p2-xpost .o2-post .entry-title {
	display: none;
}

@include breakpoint( "handset-portrait" ) {
	.tag-p2-xpost {
		margin: 0 0 0.5em !important;
	}

}

.o2-post .o2-xpost-content p {
	margin-top: 0;
	margin-bottom: 0;
}

/* Catch case where o2 is turned off */
body.highlander-enabled article.post,
body.highlander-enabled article.page {
	padding: 1.6em;
}
