/* Disable reason: Snake-cased selectors are not ours */
/* stylelint-disable selector-class-pattern */

.widget {
	font-size: pxrem(14);
	line-height: 1.4;

	& + & {
		margin-top: 3em;
	}

	select {
		max-width: 100%;
	}
}

.widget-title {
	font-size: 1.3em;
	margin-bottom: 1em;
}

/**
 * Core widgets
 */
.widget_media_image,
.widget_custom_html {
	img {
		display: block;
		border-radius: 4px;
	}
}

.widget_search {
	.searchform {
		display: flex;
		align-items: center;
		background: var(--color-white);
		padding: 12px;
		border-radius: 6px;

		&::before {
			@include gridicon("search", 1.5em, var(--color-text-medium));

			position: absolute;
			pointer-events: none;
		}
	}

	input[type="search"] {
		width: 100%;
		padding: 0 0 0 2em;
		border: 0;

		&::placeholder {
			color: var(--color-text-medium);
			opacity: 1;
		}

		&:focus::placeholder {
			opacity: 0.3;
		}
	}

	.submit {
		display: none;
	}
}

.widget_calendar {
	.wp-calendar-table {
		margin-bottom: 8px;

		caption { /* Month & Year */
			font-size: 1.1em;
			padding: 0.4em;
			text-align: center;
		}

		th,
		td {
			text-align: center;
			padding: 0.3em;
		}
	}
}

.widget_recent_comments {
	.recentcommentstexttop,
	.recentcommentstextend {
		vertical-align: middle;
	}
}

/**
 * P2020 widgets
 */

.widget-myteam {

	.widget-myteam-team-count {
		display: inline-block;
		font-size: 0.6em;
		font-weight: 400;
		margin-left: 0.6em;
	}

	#widget-myteam-manage-link {
		float: right;
		font-size: 0.6em;
		font-weight: 400;
		margin-top: 0.7em;
	}

	#widget-myteam-manage-icon {
		border: 1px solid #d0cdcd;
		border-radius: 100%;
		cursor: pointer;
		display: inline-block;
		font-size: 1.8em;
		font-weight: 400;
		height: 53px;
		line-height: 53px;
		padding: 0;
		text-align: center;
		width: 53px;
	}

}
