.entry-meta {
	@at-root .entry-header & {
		$avatar-size: 36px;

		position: relative;
		display: grid;
		grid-template-columns: [avatar] 10 + $avatar-size [main] auto [actions] 32px;
		justify-items: start;
		align-items: start;
		margin-bottom: 32px;
		font-size: pxrem(13);
		line-height: 1.3;

		.author-avatar {
			grid-row: span 2;
			width: $avatar-size;
			height: $avatar-size;
		}

		.entry-author {
			font-weight: 600;
			font-size: pxrem(15);
		}

		.entry-date {
			grid-column-start: main;
			color: var(--color-text-light);

			&:hover {
				color: var(--color-link);
			}
		}
	}
}
