@mixin o2-action-gridicon($id-slug, $icon-name) {
	.o2-#{$id-slug},
	.o2-comment-#{$id-slug} {
		&::before {
			@include gridicon($icon-name, 1em, currentColor);
		}
	}
}

.o2-dropdown-actions {
	@at-root header nav#{&} { // override over-specific upstream selector
		position: static;
		grid-column-start: actions;
		grid-row: 1 / span 2;

		ul {
			right: 0;

			@include breakpoint( "handset-portrait" ) {
				left: 0;
			}
		}

		li a:not(.o2-scroll-to-comments) { // override over-specific upstream selectors
			display: flex !important;
			align-items: center;
			font-family: var(--font-default) !important;

			&::before {
				position: static !important;
				margin-right: 8px;
				padding: 0 !important;
				width: 1em !important;
				height: 1em !important;
			}
		}

		@include o2-action-gridicon("edit", "pencil");
		@include o2-action-gridicon("short-link", "link");
		@include o2-action-gridicon("sticky-link", "pin");
		@include o2-action-gridicon("trash", "trash");
	}

	.o2-dropdown-actions-disclosure {
		position: static;

		&::before {
			display: flex;
			justify-content: center;
			align-items: center;
			position: static !important;
		}
	}
}
