.o2-comment-header {
	$avatar-size: 32px;

	@at-root header#{&} { // override upstream !important
		position: relative !important;
	}

	display: grid;
	grid-template-columns: [avatar] 10 + $avatar-size [main] auto [actions] 32px;
	align-items: center;
	padding-bottom: 0.4em;

	.avatar {
		grid-row: 1;
		width: $avatar-size;
		height: $avatar-size;
	}
}
