@mixin widget-o2-filter-gridicon($id-slug, $icon-name) {
	#o2-filter-#{$id-slug} a::before {
		@include gridicon($icon-name, 1.25em, currentColor);
	}
}


.widget_o2-filter-widget {
	font-weight: 500;

	.o2-filter-widget-item a {
		display: flex;
		align-items: center;
		padding: 8px 12px;
		border-left: 1px solid var(--color-border-dark);
		color: var(--color-text-medium);

		&::before {
			color: currentColor !important; // override over-specific upstream selector
			position: static;
			margin-right: 8px;
			padding: 0;
		}

		&:hover {
			background: transparent;
			border-left-color: currentColor;
			color: var(--color-link);
		}
	}

	a.o2-filter-widget-selected {
		color: var(--color-link) !important; // override over-specific upstream selector
		font-weight: 700;
		border-left-color: currentColor;
	}

	// Icons
	@include widget-o2-filter-gridicon("recent-updates", "house");
	@include widget-o2-filter-gridicon("recent-comments", "comment");
	@include widget-o2-filter-gridicon("my-mentions", "mention");
	@include widget-o2-filter-gridicon("no-replies", "not-visible");
	@include widget-o2-filter-gridicon("unresolved-posts", "flag");
	@include widget-o2-filter-gridicon("resolved-posts", "checkmark");
	@include widget-o2-filter-gridicon("my-posts", "user");
}
