/* SITE HEADER */

.site-header {
	background: var(--color-white);
	margin: 0 0 2em;

	@media only screen and (min-width: $tablet-portrait) {
		width: 25%;
		float: right;
		margin-bottom: 0;
	}

	@include breakpoint( "tablet-portrait" ) {
		background: none;
	}

	.hgroup {
		display: block;
		margin: 0 auto;
		padding: 1.2em;
		text-align: left;
	}

	.site-title {
		display: inline-block;
		text-align: left;
		color: var(--color-text);
		line-height: 1.4;
		padding-bottom: 5px;

		a {
			color: var(--color-text);
			line-height: 1.5;

			&:hover {
				color: var(--color-link);
			}
		}
	}

	.site-description {
		clear: both;
		display: block;
		font-family: var(--font-default);
		font-size: 1.3em;
		font-weight: 300;
		padding: 0;
		text-align: left;
		margin: 0 auto;
		margin-top: 5px;
		color: var(--color-text);
	}

	.header-image {
		display: block;
		margin: 0 auto;
	}
}

.no-sidebar .site-header {
	background: transparent;
	float: none;
	width: 100%;

	.hgroup {
		text-align: center;
	}

	.site-title {
		font-size: 3.4em;
	}
}

@include breakpoint( "tablet-portrait" ) {
	#masthead .hgroup {
		float: left;
		padding: 0;
		margin-bottom: 10px;

		.site-header,
		.site-description {
			padding: 0;
			margin: 0;
			line-height: 1;
		}
	}
}
