/* HEADINGS 1-6 */
h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: var(--font-default);
	line-height: 1.1;
}

h1 {
	font-size: 2.2em;
	font-weight: 700;
}

h2,
h3,
h4,
h5,
h6 {
	font-weight: 600;
}

h2 {
	font-size: 1.8em;
}

h3 {
	font-size: 1.6em;
}

h4 {
	font-size: 1.4em;
}

h5 {
	font-size: 1.2em;
}

h6 {
	font-size: 1.1em;
}
